body::-webkit-scrollbar {
    width: 0.5em;
    height: 0.5em;
  }
   
  body::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px black;
  }
   
  body::-webkit-scrollbar-thumb {
    background-color: black;
    outline: 1px solid gray;
  }